  -webkit-tap-highlight-color: transparent;
}

a:hover {
  text-decoration: underline;
}

textarea {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #009688;
  border-top: 1px solid #009688;
  border-radius: 0;
  outline: none;
  height: 10rem;
  width: 100%;
  font-size: 0.8rem;
  margin: 0 0 20px 0;
  min-height: 140px;
  padding: 10px 0 0;
  box-shadow: none;
  box-sizing: content-box;
  overflow-y: hidden;
  transition: all 0.1s;
}

textarea:disabled, textarea[readonly="readonly"] {
  color: rgba(0, 0, 0, 0.26);
  border-bottom: 1px dotted rgba(0, 0, 0, 0.26);
  border-top: 1px dotted rgba(0, 0, 0, 0.26);
}

textarea:disabled + label,
textarea[readonly="readonly"] + label {
  color: rgba(0, 0, 0, 0.26);
}

textarea:focus:not([readonly]) {
  border-bottom: 1px solid #26a69a;
  border-top: 2px solid #26a69a;
  box-shadow: 0 1px 0 0 #26a69a;
}

textarea:focus:not([readonly]) + label {
  color: #26a69a;
}

textarea.valid, textarea:focus.valid {
  border-bottom: 1px solid #009688;
  border-top: 2px solid #009688;
  box-shadow: 0 1px 0 0 #009688;
}

textarea.valid + label:after,
textarea:focus.valid + label:after {
  content: attr(data-success);
  color: #009688;
  opacity: 1;
}

textarea.invalid, textarea:focus.invalid {
  border-bottom: 1px solid #e91e63;
  border-top: 2px solid #e91e63;
  box-shadow: 0 1px 0 0 #e91e63;
}

textarea.invalid + label,
textarea:focus.invalid + label {
  color: #e91e63;
  opacity: 1;
}

textarea.validate + label {
  width: 100%;
  pointer-events: none;
}

textarea + label:after {
  display: block;
  content: "";
  position: absolute;
  top: 60px;
  opacity: 0;
  transition: .2s opacity ease-out, .2s color ease-out;
}

.input-field {
  position: relative;
  margin-top: 1rem;
}

.input-field.col label {
  left: 2rem;
}

.input-field.col .prefix ~ label,
.input-field.col .prefix ~ .validate ~ label {
  width: calc(100% - 3rem - 4rem);
}

.input-field label {
  color: #009688;
  position: absolute;
  top: 0.8rem;
  font-size: 1rem;
  cursor: text;
  transition: .2s ease-out;
}

.input-field label.active {
  font-size: 0.8rem;
  transform: translateY(-210%);
}

@media only screen and (max-width: 600px) {
  .input-field label.active {
    font-size: 0.9rem;
  }
}

