@media only screen and (max-width: 992px) {
  table {
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    display: block;
    position: relative;
    /* sort out borders */
  }
  table td:empty:before {
    content: '\00a0';
  }
  table th,
  table td {
    margin: 0;
    vertical-align: top;
  }
  table th {
    text-align: left;
  }
  table thead {
    display: block;
    float: left;
  }
  table thead tr {
    display: block;
    padding: 0 10px 0 0;
  }
  table thead tr th::before {
    content: "\00a0";
  }
  table tbody {
    display: block;
    width: auto;
    position: relative;
    overflow-x: auto;
    white-space: nowrap;
  }
  table tbody tr {
    display: inline-block;
    vertical-align: top;
  }
  table th {
    display: block;
    text-align: right;
  }
  table td {
    display: block;
    min-height: 1.25em;
    text-align: left;
  }
  table tr {
    padding: 0 10px;
  }
  table thead {
    border: 0;
    border-right: 1px solid #d0d0d0;
  }
